
body {
  padding : 4px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color:#1D1D20 ;
  backdrop-filter : blur(60px);
}

body::before,
body::after {
  position: absolute;
  left: 0;
  top: 0;
  content: '';
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 40%;
}

body::before {
  background: #1D1D20 ;
  filter: url(#noiseFilter);
}
.glass{
  background: rgba(98, 89, 89, 0.1);
  backdrop-filter: blur( 3px );
  -webkit-backdrop-filter: blur( 3px );
  border-radius: 10px;
  border: 0.5px ridge rgba(115, 113, 113, 0.18);
}
  
  